<template>
    <section>
        <tg-list ref="tgList" :IsShowAction="true" ModuleName="HR" SubModuleCode="Candidate" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" :ViewList="ViewList" v-on:ApplyView="OnApplyView"
            v-on:ActionButtonClick="actionButtonClick" :widgets="[]" IdentityColumn="job_post_id"
            :HeaderText="$t('CandidateList')" :listType="['List', 'Card']" :ListData="CandidateData"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_id'">                   
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')"
                        v-if="data.row.status_id == 'Active'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')"
                        v-if="data.row.status_id == 'InActive'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')"
                        v-if="data.row.status_id == 'Reject'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')"
                        v-if="data.row.status_id == 'Hired'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')"
                        v-if="data.row.status_id == 'On hold'">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'first_name'">
                        <a href="javascript:;" @click="ViewCandidateDetail(data.row.applicant_id, data.row.post_id)">{{data.row.first_name }}</a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'resume_path'">
                    <a href="#" v-if="data.column.COLUMN_NAME =='resume_path' && data.row.resume_path!=null && data.row.resume_path!=''" v-on:click="DownloadResume(data.row.resume_path,data.row.resume_name)" class="text-primary"><em class="fa fa-download pr-2"></em><span>{{data.row.resume_name}}</span></a>
                    <span v-if="data.column.COLUMN_NAME=='resume_path' && (data.row.resume_path==null || data.row.resume_path=='')" class="text-secondary"><em class="fa fa-download pr-2"></em><span>{{data.row.resume_name}}</span></span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'star_rating'">                  
                    <span v-if="data.row.star_rating==5">
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                    </span>
                    <span  v-if="data.row.star_rating==4">
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                    </span>
                    <span  v-if="data.row.star_rating==3">
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                    </span>
                    <span  v-if="data.row.star_rating==2">
                        <a><i class="fa fa-star text-warning"></i></a>
                        <a><i class="fa fa-star text-warning"></i></a>
                    </span>
                    <span  v-if="data.row.star_rating==1">
                        <a><i class="fa fa-star text-warning"></i></a>
                    </span>
                </template>
            </template>
            <template v-slot:customcard="{ data }">
                        <div class="card-box green-bg-light px-0 pb-3">
                            <div class="card-heading mt-2">
                                <a @click="commonDeleteData(data.row.applicant_id)" title="Delete" class="text-danger m-2" style="position: absolute; right:10px; top: 4px;"><em
                                        class="fa fa-times-circle"></em></a>
                                <a @click="EdittoCandidate(data.row.applicant_id, data.row.post_id)" title="Edit" class="text-success m-2" style="position: absolute; right:33px; top: 4px;"><em
                                        class="fa fa-pencil"></em></a>
                            </div>
                            <a v-bind:title="data.row.first_name" target="_self" class="nav-link  m-0 flt-rslt">                             
                                <div class="custom-control custom-checkbox custom-control-inline" style="">
                                    <input :id="data.row.applicant_id" class="chkItems custom-control-input"
                                        @change="ForCheckBoxClear($event)" v-bind:value="data.row.applicant_id"
                                        type="checkbox" />
                                    <label :for="data.row.applicant_id" type="checkbox" class="custom-control-label"></label>
                                </div>  {{ data.row.first_name }}

                                <div class="text-center py-2 float-left w-100">
                                    <div v-if="data.row.profile_path == null || data.row.profile_path == ''">
                                        <img src="https://talygenv.github.io/Vue-UI-Content/images/configure-email.png" alt="" class="w-100" style="max-height: 200px;" />
                                    </div>
                                    <div v-if="data.row.profile_path != null && data.row.profile_path != ''">
                                        {{ BindSrc(data.row.profile_path) }}
                                        <!-- <img :src="BindSrc(data.row.profile_path)" :alt="$t('ProfileImage')"
                                            class="w-100" style="max-height: 200px; min-height:200px;" /> -->
                                            <img :src="tempsrc" :alt="$t('ProfileImage')"
                                            class="w-100" style="max-height: 200px; min-height:200px;" />
                                    </div>                                   
                                </div>
                                <div class="card-detail-main">
                                    <div v-if="data.row.post_title != null">Applied For:
                                        <span>{{ data.row.post_title }}</span></div>
                                    <div v-if="data.row.post_title == null">Applied For:
                                        <span>{{ $t('NotAvailableRec') }}</span></div>

                                    <div v-if="data.row.status_name != null">Status: <span>{{ data.row.status_name }}</span>
                                    </div>
                                    <div v-if="data.row.status_name == null">Status:
                                        <span>{{ $t('NotAvailableRec') }}</span></div>


                                    <div v-if="data.row.phone_number != null" class="card-detail"><span><i
                                                class="fa fa-phone-square"></i>{{ data.row.phone_number }}</span></div>
                                    <div v-if="data.row.phone_number == null" class="card-detail"><span><i
                                                class="fa fa-phone-square"></i>{{ $t('NotAvailableRec') }}</span></div>
                                    <div class="card-detail"><span><i
                                                class="fa fa-envelope"></i>{{ data.row.email_id }}</span></div>
                                    <div v-if="data.row.date_applied_on != null" class="card-detail"><span><i
                                                class="fa fa-calendar-o"></i>{{ formatDate(data.row.date_applied_on) }}</span>
                                    </div>
                                    <div v-if="data.row.date_applied_on == null" class="card-detail"><span><i
                                                class="fa fa-calendar-o"></i>{{ $t('NotAvailableRec') }}</span></div>

                                    <div v-if="data.row.location_city != null" class="card-detail"><span><i
                                                class="fa fa-map-marker"></i>{{ data.row.location_city }}</span></div>
                                    <div v-if="data.row.location_city == null" class="card-detail"><span><i
                                                class="fa fa-map-marker"></i>{{ $t('NotAvailableRec') }}</span></div>
                                    <span class="d-block cntct single-action" v-if="data.row.star_rating == 5">
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                    </span>
                                    <span class="d-block cntct single-action" v-if="data.row.star_rating == 4">
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                    </span>
                                    <span class="d-block cntct single-action" v-if="data.row.star_rating == 3">
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                    </span>
                                    <span class="d-block cntct single-action" v-if="data.row.star_rating == 2">
                                        <i class="fa fa-star text-warning"></i>
                                        <i class="fa fa-star text-warning"></i>
                                    </span>
                                    <span class="d-block cntct single-action" v-if="data.row.star_rating == 1">
                                        <i class="fa fa-star text-warning"></i>
                                    </span>
                                </div>
                            </a>
                        </div>
            </template>
        </tg-list>
        <viewlayout v-if="ShowManageView" v-show="ShowManageView" submodulecode="HR_TALENT_POOL" modulename="Hiring"
            v-on:close="ShowManageView = false" v-bind:callbackfunction="FetchData" :usertype="usertype">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </viewlayout>
    </section>
</template>
<script>
import Schema from './CandidateSchema';
import DataService from '../../../services/DataService';
import viewlayout from "../../../components/Common/Modal/view-layout.vue";
import moment from 'moment'
export default {
    components: {
        viewlayout
    },
    data() {
        return {
            IsCandidateListView: true,
            SortBy: '',
            ShowManageView: false,
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            searchCondition: '',
            Headers: [],
            ViewList: [],
            CandidateData: [],
            StagesData: [],
            trackStatus:[],
            user_id: null,
            companyId: null,
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            leftSearchFields: Schema.leftsearchSchema,
            tempsrc:""
        }
    },
    created: async function () {
        var vm = this;
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.user_id = this.GetUserInfo.ID
        await Schema.bindleftCommonSearchdropdown(vm, "HIRING", "HR_LOCAT");
        await Schema.bindleftCommonSearchdropdown(vm, "HIRING", "JOB_POST");
        vm.GetDdlRecords_ViewList();
        vm.FetchData();
    },
    methods: {
        
        BindSrc: function (filepath) { 
             var path = filepath.substring(0, filepath.lastIndexOf("/") + 1);
             var fileName = filepath.substring(filepath.lastIndexOf("/") + 1, filepath.length);
             let Obj={
                path:path,
                fileName:fileName
             };
             var url=JSON.stringify(Obj);
             DataService.GetFileSrc(url).then((response) => {                
                if(response.data !=''){      
                    this.tempsrc = response.data
                }  
             });
        },
        ForCheckBoxClear: function (ths) {
            // debugger
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.listheaderbuttons[2].isdisabled = false; 
            }
            else {
                vm.listheaderbuttons[2].isdisabled = true;   
            }
        },        
        formatDate: function (value) {
            if (value) {
                return moment(value, "YYYY-MM-DD").format(String('MM/DD/YYYY'))
            }
        },
        FetchData: function () {
            var vm = this;
            if (!vm.isBlank(vm.searchCondition)) {
                vm.PageNumber = 1;
            }
            var url = `pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&firstname=${vm.firstname}&langCode=en`;
            if (!vm.isBlank(this.searchCondition)) {
                url += `&searchCondition=${this.searchCondition}`;
            }
            DataService.CandidateList(url).then((response) => {                
                if (response.data != null && response.data != '') {
                    vm.Headers = response.data.schema;                    
                    vm.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "first_name" || column.COLUMN_NAME == "status_id" || column.COLUMN_NAME == "resume_path" || column.COLUMN_NAME == "star_rating") {                           
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                        if (column.DATA_TYPE == 'date') {
                            column.DATA_TYPE = 'DATE';
                        }
                        if (column.DATA_TYPE == 'select' || column.DATA_TYPE == 'string') {
                            column.DATA_TYPE = 'text';
                        }
                    });
                    if (response.data.data.length > 0) {
                        vm.CandidateData = response.data.data;                        
                        vm.CandidateData.forEach(function (row) {
                            if(row.is_sample_data === true){
                                row.additionalClass = "sample-bar";
                            }
                        });
                        vm.StagesData = response.data.stage_data;
                        vm.TotalRecords = response.data.data[0].total_records;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.listheaderbuttons[2].isdisabled = true;
                        vm.listheaderbuttons[1].isdisabled = true;
                        $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                    }
                    else {
                        vm.CandidateData = [];
                        vm.TotalRecords = 0
                    }
                }
            });
        },
        actionButtonClick: function (callbackfunction, item, index, event) {  
            switch (callbackfunction) {
                case "EdittoCandidate":
                    this.EdittoCandidate(item.applicant_id, item.post_id);
                    break;
                case "DeleteOnAction":
                    this.commonDeleteData(item.applicant_id);
                    break;
                case "ApplyForMore":
                    this.ApplyForMore(item.applicant_id);
                    break;
                case "MakeOffer":
                    this.MakeOffer(item.applicant_id, item.post_id);
                    break;
                case "ViewonAction":
                    this.ViewCandidateDetail(item.applicant_id, item.post_id);
                    break;                    
            }
        },
        ApplyForMore: function (ApplicantId) {
            var vm = this;
            vm.$router.push({ name: 'HRAddAsCandidate', params: { id: ApplicantId } });
        },
        MakeOffer: async function (ApplicantId, JobId) {
            var vm = this;
            var params = `applicantId=${ApplicantId}&jobId=${JobId}`
            await DataService.GetOfferStatus(params).then(function (response) {
                if (response.data.status != "Failure" && response.data != '') {
                        vm.trackStatus = response.data.result.DATA[0];
                        if (vm.trackStatus.status != null && vm.trackStatus.status.includes("Offer letter sent")) {
                            vm.confirmR(vm.$t("ConfirmAlreadySent"), true, 'Confirm', function () {
                                vm.$router.push({ name: 'HRMakeOffer', params: { id: ApplicantId, JobId: JobId } });
                            });
                        }
                        else {
                            vm.$router.push({ name: 'HRMakeOffer', params: { id: ApplicantId, JobId: JobId } });
                        }
                    }
                    else
                    {
                        vm.$router.push({ name: 'HRMakeOffer', params: { id: ApplicantId, JobId: JobId } });
                    }
            })            
        },
        ViewCandidateDetail:function (ApplicantId, JobId){
            var vm = this;
            vm.$router.push({ name: 'CandidateDetail', params: { id: ApplicantId, JobId: JobId } });
        },
        ManageView: function () {
            var vm = this;
            vm.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal("show");
            }, 1000);
        },
        DownloadResume: async function (path, name) {
            var vm=this;
            var fileName = name;
            var filePath = path;
            if (filePath == "" && fileName == "" || filePath == null) {
                vm.ShowAlert(vm.$t("NoAttachment"), "warning", true, vm.$t("Alert"));
            } else {
                var url = `filePath=${filePath}&fileName=${fileName}`;
                await DataService.DownloadFile(url).then(async function (response) {
                    const byteData = response.data;
                    const fileURL = `data:image/png;base64;base64,${byteData}`;
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        GetDdlRecords_ViewList: async function () {
            var vm = this;
            vm.ViewList = [];
            var url = `moduleName=Hiring&subModuleCode=HR_TALENT_POOL&viewId=0&pageSize=100&pageNum=1&sortBy=&sortExp=`;
            await DataService.GetViewList(url).then(function (response) {
                if (response.data && typeof (response.data.DATA) != 'undefined') {
                    vm.ViewList = response.data.DATA;
                }
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {

                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if (vm.ViewListId > 0) {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;
                    } else {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                    }
                }, 1000);

            });
        },
        DefaultView: function () {

            var vm = this;
            var obj = {
                ModuleName: 'Hiring',
                SubmoduleCode: 'HR_TALENT_POOL'
            };
            var objectPostString = JSON.stringify(obj);
            DataService.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        OnApplyView(viewId) {
            var vm = this;
            if (viewId == '0') {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'Hiring',
                    SubmoduleCode: 'HR_TALENT_POOL'
                };
                var objectPostString = JSON.stringify(obj);
                DataService.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.$emit('RefreshManageView');
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                case 'Reject':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                case 'Hired':
                    returnClass = 'px-2 text-center text-white d-block bg-primary';
                    break;
                case 'On hold':
                    returnClass = 'px-2 text-center text-white d-block bg-secondary';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddtoCandidate":
                    this.AddtoCandidate();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "ManageView":
                    this.ManageView();
                    break;
                case "ForwardCandidatesCV":
                    this.ForwardCandidatesCV();
                    break;
            }
        },
        commonDeleteData: function (event) {
            var vm = this;         
            var val = "";
            if (event > 0) {
                val = event;
            } else {
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            if (val.length > 0 || event > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Candidate")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "Hiring",
                        subModuleCode: "HR_TALENT_POOL",
                        refCode: "TALYGEN_JOB_POST_APPLICANT"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {

                        if (response.data[0].Status == "Success") {
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Candidate')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Candidate')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        ForwardCandidatesCV:function(){
            var vm = this;
            var ids = '';
            var val = '';
            var postid = '';
            var valpostid = '';
            var emailid = '';
            var valemailid = '';        
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val(); 
                    vm.CandidateData.forEach((item,index)=>{
                        if(item.applicant_id== parseInt($(this).val())){ 
                            valpostid += item.post_id+',';
                            valemailid +=item.email_id+',';
                        }
                    })                  
                });                   
            var post_id = valpostid.slice(0, -1);
            var email_id = valemailid.slice(0, -1);
                postid = post_id;
                emailid = email_id;
            var queryString = "aid=" + val + "&pid=" + postid;
            var postString = {
                 queryString: queryString,
                 source: "ForwardCv"
            }            
            DataService.AddQueryString(postString).then((response)=>{                
                if(response.data.code == "1"){
                    if (response.data.result != null) {
                        this.$router.push({ name: 'ForwardCv', params: { qid: response.data.result,aid:val,pid:postid} });
                    }
                }
            })
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                     case 'Delete':
                        action.isVisible = true;
                        break;
                     case 'Apply For More':
                        action.isVisible = true;
                        break;
                     case 'Make Offer':
                        action.isVisible = row.status_name=='Hired' || row.status_name=='Reject'?false:true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        AddtoCandidate: function () {
            this.$router.push({ name: 'ManageCandidate', params: { reqfrom: 'HR/CandidateList', id: 0, pid: null, aid: null, uid: null} });
        },
        EdittoCandidate: function (id, pid,path) {
            this.$router.push({ name: 'ManageCandidate', params: { reqfrom: 'HR/CandidateList', id: id, pid: pid, aid: null, uid: null} });
        }
    }
}
</script>
